import React, { useState } from 'react'
import { Link, useHistory } from 'react-router-dom'
import InputPasswordToggle from '@components/input-password-toggle'
import { Row, Col, Form, FormGroup, Label, Input, Button } from 'reactstrap'
import { Offline, Online } from "react-detect-offline"
import SubTitle from '../../components/SubTitle'
import './Signin.scss'

// ** firebase
import firebase from 'firebase/app'

const Signin = () => {
  const history = useHistory()

  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [checking, setChecking] = useState(false)

  const handleChange = ({ target: { name, value } }) => {
    switch (name) {
      case "email":
        setEmail(value)
        break
      case "password":
        setPassword(value)
        break
    }
  }

  const handleSubmit = e => {
    e.preventDefault()
    setChecking(true)

    firebase.auth()
      .signInWithEmailAndPassword(email, password)
      .then(() => {
        setChecking(false)
        history.push('/home')
      })
      .catch(err => {
        setChecking(false)
        alert(err.message)
      })
  }

  return (
    <div className='Signin'>
      <Row className='justify-content-center'>
        <SubTitle title={"Sign In"} className="m-t-40 w-100" />
        <Offline>
          <div className="h3">No connectivity, please try again when it's available</div>
        </Offline>
        <Online>
          <Col className='d-flex align-items-center auth-bg px-2' lg='5' sm='12'>
            <Col className='px-xl-2 mx-auto' sm='8' md='6' lg='12'>
              <Form className='auth-login-form mt-2' onSubmit={handleSubmit}>
                <FormGroup>
                  <Label className='form-label' for='login-email'>
                    Email
                  </Label>
                  <Input type='email' id='login-email' placeholder='john@example.com' name="email" onChange={handleChange} autoFocus required />
                </FormGroup>
                <FormGroup>
                  <div className='d-flex justify-content-between'>
                    <Label className='form-label' for='login-password'>
                      Password
                    </Label>
                    <Link to='/forgot-password'>
                      <small>Forgot Password?</small>
                    </Link>
                  </div>
                  <InputPasswordToggle className='input-group-merge' id='login-password' name="password" onChange={handleChange} required />
                </FormGroup>
                <Button.Ripple type="submit" color='primary' block className="mt-2 flex justify-content-center align-items-center">
                  {checking &&
                    <span className="spinner-border spinner-border-sm mr-1" role="status" aria-hidden="true"></span>
                  }
                  Sign in
                </Button.Ripple>
              </Form>
              <p className='text-center mt-2'>
                <span className='mr-25'>New to our platform?</span>
                <Link to='/signup'>
                  <span>Create an account</span>
                </Link>
              </p>
            </Col>
          </Col>
        </Online>
      </Row>
    </div>
  )
}

export default Signin